.about {
	padding: 144px 0 154px;
	position: relative;
	min-height: 650px;
	@include sm {
		padding: 60px 0 0; }
	&:before {
		content: '';
		position: absolute;
		top: 50px;
		left: 0;
		background-color: $blue;
		width: 42.4%;
		// padding-bottom: 37.4%
		height: 84.6%;
		border: 2px solid $white;
		border-left: none;
		box-shadow: 0 0 0px 50px $blue;
		z-index: -1;
		@include sm {
			display: none; } }
	&__inner {
		max-width: 1162px;
		display: flex;
		justify-content: center;
		align-items: center;
		position: relative;
		@include sm {
			flex-wrap: wrap; } }
	&__img {
		width: 50%;
		img {
			display: block;
			max-width: 100%;
			// +sm
			// 	min-height: 255px
			@include ms {
				position: absolute;
				top: 0;
				left: 50%;
				transform: translateX(-50%);
				max-width: none;
				width: 400px; } }
		@include sm {
			width: 100%; }
		@include ms {
			width: 100%;
			position: relative;
			height: 255px;
			overflow: hidden; } }
	&__text {
		width: 50%;
		padding-left: 4em;
		@include md {
			padding-left: 2em; }
		@include sm {
			padding-left: 0;
			width: 100%; } }
	&__title {
		padding-bottom: 0.5em;
		margin-top: -20px;
		@include sm {
			margin-top: 50px; }
		&.title {
			@include md {
				font-size: 30px; } } }
	&__dots {
		position: absolute;
		right: 28px;
		bottom: -107px; } }
// 											DOTS
.dots {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	max-width: 54px;
	width: 100%;
	&__item {
		width: 8px;
		height: 8px;
		margin: 4px 5px;
		background-color: $black; }
	@include sm {
		display: none; } }

// 												SEMINAR
.seminar-block {
	padding-top: 150px;
	@include md {
		padding-top: 78px; }
	&__row {
		padding-top: 70px;
		@include md {
			padding-top: 37px; } }
	&__col {
		@include sm {
			&:not(:first-child) {
				margin-top: 30px; } } } }

// 											COURSE
.course-block {
	padding-top: 155px;
	@include md {
		padding-top: 80px; }
	&__head {
		display: flex;
		align-items: center;
		justify-content: space-between; }
	&__title {}
	&__row {
		padding-top: 65px;
		@include md {
			padding-top: 20px;
			margin: 0 auto; } }
	&__col {
		@include md {
			margin-top: 16px; } }
	&__btn {
		@include sm {
			display: none; }
		&_mob {
			display: none;
			.btn {
				min-width: 288px; }
			@include sm {
				display: block;
				text-align: center;
				margin-top: 34px; } } } }


// 										FORM
.main-feedback {
	&__inner {
		@include md {
			padding: 0; } }
	&__wrap {
		padding-top: 150px;
		@include md {
			padding-top: 80px; } } }

// 											EVENT
.event-block {
	&-wrap {
		padding-top: 150px;
		@include md {
			padding-top: 60px; } }
	&__inner {
		@include ms {
			padding: 0; } }
	&__head {
		display: flex;
		align-items: center;
		flex-wrap: wrap; }
	&__title {
		max-width: 33%;
		@include md {
			max-width: none;
			width: 100%; }
		@include ms {
			padding: 0 16px; } }
	&__list {
		padding-top: 60px;
		@include sm {
			// max-width: 400px
			// margin: 0 auto
			padding-top: 30px;
			// &.row
 } }			// 	margin: 0
	&__col {
		@include sm {
			&:not(:first-child) {
				margin-top: 30px; } } }
	&__tabs {
		margin-top: 30px;
		margin-left: auto;
		@include md {
			margin-left: 0; }
		@include sm {
			display: none; } }
	&__btn {
		margin-top: 30px;
		@include lg {
			display: none; }
		&_mob {
			display: none;
			.btn {
				min-width: 288px; }
			@include lg {
				display: block;
				text-align: center;
				margin-top: 40px; } } } }

// 											INSTRUCTOR
.instructor-block {
	padding-top: 150px;
	@include lg {
		padding-top: 78px; }
	&__head {
		display: flex;
		align-items: center;
		justify-content: space-between; }
	&__row {
		padding-top: 70px;
		@include md {
			margin: 0 auto;
			padding-top: 39px; } }
	&__col {
		margin-top: 16px; }
	&__btn {
		@include sm {
			display: none; }
		&_mob {
			display: none;
			.btn {
				min-width: 288px; }
			@include sm {
				display: block;
				text-align: center;
				margin-top: 30px; } } } }

// 											testimonial
.testimonial-block {
	padding-top: 150px;
	@include lg {
		padding-top: 78px; }
	&__head {
		display: flex;
		align-items: center;
		justify-content: space-between; }
	&__row {
		padding-top: 70px;
		@include sm {
			white-space: nowrap;
			overflow: hidden;
			opacity: 0;
			visibility: hidden;
			transition: opacity .3s, visibility .3s;
			&.slick-initialized {
				white-space: normal;
				overflow: visible;
				opacity: 1;
				visibility: visible; } }
		@include md {
			padding-top: 24px; } }
	&__col {
		@include md {
			margin-top: 16px; } }
	&__btn {
		@include md {
			display: none; }
		&_mob {
			display: none;
			.btn {
				min-width: 288px; }
			@include md {
				display: block;
				text-align: center;
				margin-top: 25px; } } } }

// 										CONTACTS
.contacts-block {
	padding: 160px 0 128px;
	overflow: hidden;
	@include md {
		padding: 78px 0 0; }
	.contacts-page & {
		padding: 239px 0;
		@include md {
			padding: 101px 0 0; } }
	&__inner {
		@include sm {
			padding: 0; } }
	&__title {
		@include sm {
			padding: 0 16px 16px; } }
	&__map {
		padding-top: 70px;
		@include md {
			padding-top: 0; } } }
